/*_icons.scss*/

.svg-icon{

}

.svg-icon svg{
width: 100%;
height:100%;
float: left;
 }

 .svg-icon.white-icon svg{
fill:$white;
stroke:$white; // if strokes are used; apply same colour
}

 .svg-icon.black-icon svg{
fill:$black; 
stroke:$black;// if strokes are used; apply same colour
}


.inline-icon{
width: 2.5vw; // experimental - how to approach better? - tie in in scaling
}

.inline-icon.right{
float: right;
}

.arrow.more{
@include cssvalue(width, 0.2);
@include cssvalue(height, 0.2);
position: absolute;
right: 0;
@include cssvalue(top, 0.18);

  svg{
  width: 100%;
  height:100%;
  float: left;
  }

}//.arrow.more

