@mixin center-align($position: relative, $adjust: 0) {

  @if ($adjust!=0) {
    top: calc(50% + #{$adjust});
  } @else {
    top: 50%;
  }
  
  left: 50%;
  position: $position;
  transform: translate(-50%, -50%);

}

