// stare opacity pulse key frames



@-webkit-keyframes pulse {
    0% { -webkit-transform: scale(0.9); opacity: 0.2; }
    50% { -webkit-transform: scale(1); opacity: 1; }
    100% { -webkit-transform: scale(0.9); opacity: 0.2; }
}

@keyframes pulse {
    0% { transform: scale(0.9); opacity: 0.2; }
    50% { transform: scale(1); opacity: 1; }
    100% { transform: scale(0.9); opacity: 0.2; }
}
// end opacity pulse key frames


// strart grayscale animations

@keyframes tograyscale {
   0% {
   
   -webkit-filter: grayscale(0%); /* Ch 23+, Saf 6.0+, BB 10.0+ */
          filter: grayscale(0%); /* FF 35+ */
   }
   100%{
   -webkit-filter: grayscale(100%); /* Ch 23+, Saf 6.0+, BB 10.0+ */
         filter: grayscale(100%); /* FF 35+ */
   }

}

@keyframes tocolour {
   0% {
   
   -webkit-filter: grayscale(100%); /* Ch 23+, Saf 6.0+, BB 10.0+ */
          filter: grayscale(100%); /* FF 35+ */
   }
   100%{
   -webkit-filter: grayscale(0%); /* Ch 23+, Saf 6.0+, BB 10.0+ */
         filter: grayscale(0%); /* FF 35+ */
   }

}

// end grayscale animations


@mixin grayscale-in{
 animation: tograyscale 2s ease-in-out forwards;
}

@mixin grayscale-out{
 animation: tocolour 1s ease-in-out forwards;
}




