*,
*::before,
*::after {
  box-sizing: border-box;
}

/* DEV GRID */

  #dev-grid.container{
    position: fixed;
    top: 0px;
    height: var(--site-height);
    width: 100%;
    pointer-events: none;
    z-index: 99;
    
    .dev-toggle{
    background: $dev-grid-color-two;
    height: 20px;
    width: 20px;
    position: fixed;
    right: 0px;
    bottom:0px;
    pointer-events: initial;
    cursor: pointer;
    }

    .dev-grid-item{
    opacity: 0;
    height: var(--site-height);
    border: 1px solid $dev-grid-color-one;

       &:after{
          content: "";
          width: inherit;
          display: block;
          border: 1px solid $dev-grid-color-two;
          //margin: 2.5px;
          height: 100%;
       }
    }

 

  } //#dev-grid.container

  body.dev-on{

      #dev-grid.container{

        .dev-grid-item{
          opacity: 1;
        }
      }
  } /* dev-grid on*/


/* END DEV GRID */

/* SITE GRID */

// grid container
.grid {
  display: flex;
  flex-flow: row wrap;
  margin-bottom: 0;
  position: relative;/* I added this to fix the news sticky title - but might affect the whole site? REview accordingly */
}


/* item sizes */


// loop over the breakpoints - for grid item sizing
@each $key, $value in $breakpoints {
  
  @for $i from 1 through $items {
    .grid-item-#{$key}-#{$i} {
     // flex: 0 0 100%;
      @include break($key) {
        flex: 0 0 #{$i / $items * 100%};
      }
    }
  

    .push-item-left-#{$key}-#{$i} {
       @include break($key) {
      margin-left: #{$i / $items * 100%};
      }
    }
    .push-item-right-#{$key}-#{$i} {
       @include break($key) {
      margin-right: #{$i / $items * 100%};
      }
    }

    .padding-item-left-#{$key}-#{$i} {
       @include break($key) {
      padding-left: #{$i / $items * 100%};
      }
    }
     .padding-item-right-#{$key}-#{$i} {
       @include break($key) {
      padding-right: #{$i / $items * 100%};
      }
    }



  }

  @for $i from 1 through $devitems {
      .outer-grid-item-#{$key}-#{$i} {
        flex: 0 0 100%;
        @include break($key) {
          flex: 0 0 #{$i / $devitems * 100%};
        }
      }

      .size-item-#{$key}-#{$i} {
       // flex: 0 0 100%;
        @include break($key) {
          width: #{$i / $devitems * 100%};
        }
      }

  } //@for $i from 1 through $devitems {



  @for $i from 1 through $devitems {
      .dev-grid-item-#{$key}-#{$i} {
        flex: 0 0 100%;
        @include break($key) {
          flex: 0 0 #{$i / $devitems * 100%};
        }
      }
  }

} //@each $key, $value in $breakpoints 



/* z-index generator for blocks*/

 @for $i from 1 through $z-index-items {
    .block-z-index-#{$i} {
    //  flex: 0 0 #{$i / $devitems * 100%};
    z-index:#{$i};
    }
  }
  
/* end z-index generator for blocks*/

/* gutter */

