/* DEV GRID */
#dev-grid.container {
  position: fixed;
  top: 0px;
  height: var(--site-height);
  width: 100%;
  pointer-events: none;
  z-index: 99; }
  #dev-grid.container .dev-toggle {
    background: green;
    height: 20px;
    width: 20px;
    position: fixed;
    right: 0px;
    bottom: 0px;
    pointer-events: initial;
    cursor: pointer; }
  #dev-grid.container .dev-grid-item {
    opacity: 0;
    height: var(--site-height);
    border: 1px solid red; }
    #dev-grid.container .dev-grid-item:after {
      content: "";
      width: inherit;
      display: block;
      border: 1px solid green;
      height: 100%; }

body.dev-on #dev-grid.container .dev-grid-item {
  opacity: 1; }

