/* end view module */
.carousel {
  width: 100%;
  height: 100%;
  position: absolute;
  transform-style: preserve-3d; }

.carousel__cell:before {
  content: '';
  display: block;
  overflow: hidden;
  width: 100%;
  height: 100%; }

.carousel__cell {
  position: absolute;
  width: 100%;
  height: 100%;
  transition: transform 1s, opacity 1s;
  -webkit-transform-style: preserve-3d;
  -webkit-backface-visibility: hidden;
  transform-style: preserve-3d;
  backface-visibility: hidden; }
  .carousel__cell img.front {
    opacity: 0.6;
    position: absolute;
    top: 5%;
    left: 5%;
    z-index: 2;
    width: 90%; }
  .carousel__cell#release-404 img.front {
    top: 16.25%;
    left: 16.25%;
    width: 67.5%; }

.vertical .carousel__cell img.front {
  opacity: 0.2; }

.carousel__cell:after {
  right: 0%;
  bottom: 0%;
  position: absolute;
  top: 0px;
  left: 0px;
  width: 100%;
  background-size: 90%;
  background-repeat: no-repeat;
  background-position: center;
  -webkit-transform: rotateY(180deg);
  -webkit-transform-style: preserve-3d;
  -webkit-backface-visibility: hidden;
  transform: rotateY(180deg);
  transform-style: preserve-3d;
  backface-visibility: hidden;
  z-index: 2; }

#release-404.carousel__cell:after {
  background-size: 67.5%; }

.carousel__cell.active .description-wrap {
  opacity: 1;
  font-weight: 400; }

.carousel__cell.active img.front {
  opacity: 1; }

