@keyframes rollingright{
  from { transform: rotate(0deg); }
  to { transform: rotate(180deg); }
}
@keyframes moveright {
  0%   { left: 0px; }
  100% { left: 12.5vw;//15vw;//200px; 
  }
}

@keyframes rollingleft{
  from { transform: rotate(180deg); }
  to { transform: rotate(0deg); }
}
@keyframes moveleft {
  0%   { left: 12.5vw;//15vw;//200px; 
  }
  100% { left: 0px; }
}
 
