.record-square-container{
display: block;
height:37.5vw;//45vw;//600px; 
padding:0;// 100px 0px 100px 0px; // Iremoved the padding changed to vw
margin:auto;
width: 100%;
position: relative;
overflow:hidden;
padding-top: 6.25vw;
padding-bottom: 6.25vw;

    .flip-card {
    position: relative;
    margin: auto;
    display: block;
    width: 25vw;
    z-index: 92;
    perspective: 1000px; /* Remove this if you don't want the 3D effect */
    height: 100%;

          /* This container is needed to position the front and back side */
          .flip-card-inner {
          position: relative;
          width: 100%;
          height: 100%;
          text-align: center;
          transition: transform 0.8s;
          transform-style: preserve-3d;
          }

         .flip-card-front, 
         .flip-card-back {
          position: absolute;
          width: 100%;
          height: 100%;
          backface-visibility: hidden;

            img{
              backface-visibility: hidden;
            }

          }

          .flip-card-front {
           // background: red;

            img{
             // opacity: 1;

            }
          }

           .flip-card-back {
            transform: rotateY(180deg);
           // background: $black;
              background-size: contain;

             img{
             // opacity: 0.5
              
            }

            .record-sleave.back{
           //  width: 100%;

             }

          }// .flip-card-back

      
    }//.flip-card 
 
    &.active{
    pointer-events:none;
 
        .flip-card-inner {
        transform: rotateY(180deg);
        }
  
    }//&.active

}//.record-square-container

