.scene {
position: relative;
width: 40vh;
height: 40vh;
margin: 30vh auto 30vh auto;
perspective: 1000px;
  
    // because to small on my mobile

    @media (min-width: map-get($breakpoints,md)) {
    width: 60vh;
    height: 60vh;
    margin: 20vh auto 20vh auto;
    }     

}//.scene

.scene-height{
height: auto;
overflow: hidden;
position: relative;
width: 100%;
pointer-events:none; //tmep rmoe
margin-top: calc(var(--site-height) * -1); // review this - its the ofset to cater for our scenefixer
padding-bottom: 0vh; // it make the rotating positing nicely but not lining up wiht my active sluders

    .height-item{
    height:var(--site-height);
    overflow: hidden;
    position: relative;

        a{
        overflow: hidden;
        width: 100%;
        height: inherit;
        display: block;
        position: relative;
        }

        .code-wrap{
        display: block;
       // position: sticky; 
        //@include prefix(position, sticky);
        position: -webkit-sticky;
        position: -moz-sticky;
        position: -ms-sticky;
        position: -o-sticky;
        position: sticky;

        // @include blur; removed because causing lag
        opacity: 0.8;
        top: 10vh;
        width: 100%;
        height: 12.5vw;
        line-height: 12.5vw;
        @include sideheader;
        text-align: left;
        -webkit-transform: rotate(-90deg);
        -moz-transform: rotate(-90deg);
        -o-transform: rotate(-90deg);
        -ms-transform: rotate(-90deg);
        transform: rotate(-90deg);
        transform-origin: 0 0;

            .release-code{
            position: relative;
            color: white;
            width: 100%;
            height: inherit;
            text-transform: uppercase;
            font-weight: $lightweight;
            z-index: 9;
            text-align: center; // center and offset so it visually aligns
            margin-left: -50%;
