body.single-releases{


  #artist-details{
  @include padding(padding-bottom, 4);



  }

  .cover-image{
    height: var(--site-height);
    position: relative;
    width: 100%;
    display:block;
    background-size: cover;
    background-position: center;
  }


  .banner-image{
    height: 40vh;
    position: relative;
    width: 100%;
    display:block;
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;;
  }

  .release-format{
//  @include cssvaluegutter(padding-left, 0.1);
  @include cssvalue(font-size, 0.5);
  text-transform: uppercase;
  font-weight: $lightweight;
  display: block;
  width: 100%;
  text-align: center;
  border-top: 1px solid $white_0-25;
  border-bottom: 1px solid $white_0-25;
  @include padding(padding-top, 0.5);
  @include padding(padding-bottom, 0.5);

    .format{
    font-weight: $mediumweight;
    display: inline-block;
    }

  }//.release-format

  div#release-links{
  text-align: center;
  @include padding(padding-top, 1);
  @include padding(padding-bottom, 1);

    .buy-description{
      margin: auto;
      width: 100%;
     font-weight: $boldweight;

     @include lineheight; 
     @include padding(padding-top, 0.5);
     @include padding(padding-bottom, 0.5);
    }

    ul{
    width: 100%;
    @include padding(padding-top, 0.5);
    @include padding(padding-bottom, 0.5);
    }

    li{
    //  display: block;
      float: none;
      position: relative;
    }

  } // section#release-links

  div#release-description{
  text-align: center;

    .description{
    margin: auto;
    }


  } // section#release-description


  div#release-music{
  @include cssvalue(padding-bottom, 1);
 
    iframe{
    float: left;  
    } 

    .music-item{
    margin: auto;
    background: $white_0-1;// temp - find a way to remove this once bc loaded - review - not sure if posisble. No way to get loading response from iFrame?
    overflow: hidden;
    position: relative;
    min-height: 200px;
    max-width: 100%;
    min-width: 200px;
    padding:0px;// this over-rides grid-item classes

      .gradient-loader-wrapper{
        position: absolute;
        width: 100%;
        height: 100%;
        padding: 20%;
        top: 0%;
        bottom: 0px;
        right: 0px;
        left: 0px;
        pointer-events:none;

          .gradient-loader{
          @include cssvalue(margin-top, -0.125);
          @include cssvalue(height, 1);
          top: 50%; // this crossbrowser - review?
          position: relative;
          background: repeating-linear-gradient(to right, $black_zero_p 0%, $white_0-1 50%, $black_zero_p 100%);
          width: inherit;
          background-size: 200% auto;
          background-position: 0 100%;
          animation: loadergradient 2s infinite;
          animation-fill-mode: forwards;
          animation-timing-function: linear;
          @include cssvalue(height,0.25);
          z-index: -1;
         // border-radius: 50%;//review - cross browser
          }

      }///gradient-loader-wrap
    
     }//.music-item
 
  } // #release-music


  
  section#release-artists{
 
    .music-item{
    margin: auto;
    }

    a{
    overflow: hidden;
    display: block;
 
     .artist-title{
        @include sideheader;
        @include smaller;
        width: 40vh;// the height of the image banners
        animation: blurout 400ms ease-in forwards;

      }

    }//a
 
    a:hover{
 
      .artist-title{
        animation: blurin 400ms ease-in forwards;
      }

    }//a:hover

  } // section#release-artists

  &.is-touch{

    section#release-artists{
 
        .music-item{
         }

        a{
           .artist-title{
            animation: none;

          }

        }//a
     
        a:hover{
     
          .artist-title{
            animation:none;
           }

        }//a:hover

      } // section#release-artists



  } //&.is-touch

} /* body.single-releases */


